<template>
  <div class="kanban">
    <div class="titleHead">
      <p id="pHead">{{title}}</p>
      <!-- <p id="time">当前上机人员：{{ upDevUser }}</p> -->
    </div>
    <div class="kanbanContent">
      <div class="leftContent">
        <div class="left1">
          <div class="left1title">今日运行状态</div>
          <div class="left1content">
            <div class="left1_1">
              <div class="left1_1content" id="echarts2" ref="echarts2"></div>
              <div class="left1_1title">运行状态分布</div>
            </div>
            <div class="left1_2">
              <pie-progress chartName="OEE" refName="oeeChart" width="100%" height="80%" :chartData="oeeChartData"></pie-progress>
              <!-- <div class="left1_2content" id="echarts3" ref="echarts3"></div> -->
              <div class="left1_2title">
                OEE
                <!-- 设备利用率 -->
                <!-- <span id="UtilizNum">{{ UtilizNum }}%</span> -->
              </div>
            </div>
          </div>
          <div class="line"></div>
        </div>
        <div class="left2">
          <div class="left2title">设备效率</div>
          <div class="left2content" id="echarts1" ref="echarts1"></div>
          <div class="line"></div>
        </div>
        <div class="left3">
          <div class="left3title">设备告警</div>
          <div class="left3content">
            <table id="warnTable" >
              <tr v-for="(item, index) in warnList" :key="index" >
                <td><div class="warnRed" ></div></td>
                <td>
                  <p class="active" >{{item.deviceName}}   于  {{item.warningStartTime}} </p>
                  <p class="active"  >报警内容：{{warnFunc(item)}}</p>
                </td>
              </tr>
            </table>
          </div>
          <div class="line"></div>
        </div>
      </div>
      <div class="rightContent">
        <div class="right1">
          <div class="right1title">设备分布</div>
          <div class="devLegen">
            <div class="yxLege"></div>
            <div class="legeTitle">运行</div>
            <div class="kxLege"></div>
            <div class="legeTitle">空闲</div>
            <div class="lxLege"></div>
            <div class="legeTitle">离线</div>
            <div class="gjLege"></div>
            <div class="legeTitle">告警</div>
          </div>
         <div class="right1content" id="right1content" ref="right1content" v-html="devStateHtml"></div>
        </div>
        <div class="right2">
          <div class="right2title">宕机时间</div>
          <div class="right2content" id="echarts4" ref="echarts4"></div>
        </div>
        <div class="right2">
          <div class="right2title">良品率</div>
          <div class="right2content" id="echarts5" ref="echarts5"></div>
        </div>
        <div class="right2">
          <div class="right2title">稼动率</div>
          <div class="right2content" id="echarts6" ref="echarts6"></div>
        </div>
        <div class="right2">
          <div class="right2title">产能</div>
          <div class="right2content" id="echarts7" ref="echarts7"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
import { KANBAN_CONFIG } from '@/utils/ajaxConfigure';
import PieProgress from '@/components/common/kanban/PieProgress';
// 导入所需接口
import {
  deviceRealLiveGetDeviceRealState,
  deviceRealLiveGetDeviceStateReportStatistics,
  deviceManagerGetCheckCount,
  deviceManagerGetRepairTaskCount,
  deviceManagerGetMaintainTaskCount,
  deviceReportGetDeviceDayUtilization,
  deviceEarlyWarningGetDeviceWarnRecord,
  deviceRealLiveGetPageByDevice
} from '@/api/kanban';
import { listWarnStatisticsChart, oeeChart, listPlannedProduction } from '@/api/xwReport';
import { listRealUserWorkRecord } from '@/api/andon';
import {
  STATUS_CODE
  // TITLE
} from '@/utils/constantsFile';
import {
// showErrorNotify
} from '@/utils/notify';
// 导入通用方法
import {
  // getGroupId,
  getTodayFirstDay,
  getTodayLastDay,
  getSearchToday
} from '@/utils/publicFunction2';

export default {
  name: 'kanban',
  components: {
    PieProgress
  },
  data() {
    return {
      title: '',
      groupId: 0,
      allId: 0,
      showRun: false,
      timer: {},
      timer4: {},
      time: '',
      warnHtml: '',
      devStateHtml: '',
      UtilizNum: 0,
      devList: [],
      devLbIndex: 0,
      warnList: [],
      // 设备分布数据
      productTaskData: [],
      // 生产任务 每行的展示的设备数
      rowDeviceNum: 10,
      repairCard: {
        no: 0,
        do: 0,
        off: 0
      },
      maintainCard: {
        no: 0,
        do: 0,
        off: 0
      },
      checkCard: {
        no: 0,
        do: 0,
        off: 0
      },
      current: 1,
      size: 6,
      current_w: 1,
      size_w: 4,
      kanbanConfig: { title: '数字化车间看板', key: '', time: 15000 },
      electricList: [],
      todayElecticeNum: 0,
      upDevUser: '无',
      code: '',
      oeeChartData: {
        full: 0,
        value: 0,
        titleVal: '0%'
      },
      cutDevice: {
        deviceCode: '',
        todayValue: 0,
        planPro: 0
      },
      devJSON: []
    };
  },
  mounted() {
    // 拿到表格挂载后的真实DOM
    const table = this.$refs.right1content;
    // console.log(table.right1content);
    // 拿到表格中承载数据的div元素
    const divData = table;
    // // 拿到元素后，对元素进行定时增加距离顶部距离，实现滚动效果(此配置为每100毫秒移动1像素)
    this.finga(divData);

    if (this.kanbanConfig.showEnergy) {
      const table4 = this.$refs.right4content;
      const divData4 = table4;
      this.finga4(divData4);
    }
  },
  created() {
    var that = this;
    window.toDevicePage = that.toDevicePage;
    if (KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)]) {
      that.kanbanConfig = KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];
    }

    // console.log(this.getUrlKeyMain('tvNo', window.location.href));
    if (this.getUrlKeyMain('tvNo', window.location.href) !== null) {
      this.title = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].title;
      this.groupId = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].id;
      this.allId = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].allId;
      this.showRun = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].showRun;
      this.code = that.kanbanConfig.group[this.getUrlKeyMain('tvNo', window.location.href)].code;
    } else {
      this.title = that.kanbanConfig.title;
    }

    // this.kanbanConfig = KANBAN_CONFIG[this.getUrlKeyMain('cid', window.location.href)];

    // setInterval(() => {
    //   that.tick();
    // }, 1000);
    this.getDataTime();

    this.$nextTick(() => {
      this.showLineAndDev(); // 设备分布
      this.init();
    });

    setInterval(() => {
      this.getUpDevUser(); // 设备分布
    }, 35000);

    setInterval(() => {
      this.showLineAndDev(); // 设备分布
    }, 5000);

    if (that.kanbanConfig) {
      setTimeout(() => {
        setInterval(() => {
          that.init();
        }, that.kanbanConfig.time);
      }, 0);
    } else {
      setTimeout(() => {
        setInterval(() => {
          that.init();
        }, 15000);
      }, 0);
    }
  },
  methods: {
    init() {
      this.getUpDevUser();
      this.showEchartsData(); // 设备效率
      this.showEcharts2Data(); // 今日设备状态分布
      // this.showEcharts3Data(); // 设备利用率
      this.getOeeData(); // OEE
      this.showEcharts4Data(); // 宕机时间
      this.showEcharts6Data(); // 稼动率、良率、产能
      this.showDevManagerData(); // 设备管理

      this.showWarn(); // 设备预警
    },
    async getUpDevUser() {
      try {
        const res = await listRealUserWorkRecord({
          groupId: this.groupId
        }, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (res.rows.length > 0) {
            this.upDevUser = res.rows[0].realName + '(' + res.rows[0].userName + ')';
          } else {
            this.upDevUser = '无';
          }
        }
      } catch (error) {
        console.log(error);
      }
    },
    finga(divData) {
      this.timer = setInterval(() => {
        // 元素自增距离顶部1像素
        divData.scrollTop += 1;
        // 判断元素是否滚动到底部(可视高度+距离顶部=整个高度)
        if (divData.clientHeight + divData.scrollTop === divData.scrollHeight) {
          // 重置table距离顶部距离
          clearInterval(this.timer);
          setTimeout(() => {
            divData.scrollTop = 0;
          }, 2000);
          setTimeout(() => {
            this.finga(divData);
          }, 4000);
        }
      }, 50); // 滚动速度
    },
    finga4(divData) {
      this.timer4 = setInterval(() => {
        // 元素自增距离顶部1像素
        divData.scrollTop += 1;
        // 判断元素是否滚动到底部(可视高度+距离顶部=整个高度)
        if (divData.clientHeight + divData.scrollTop === divData.scrollHeight) {
          // 重置table距离顶部距离
          clearInterval(this.timer4);
          setTimeout(() => {
            divData.scrollTop = 0;
          }, 2000);
          setTimeout(() => {
            this.finga4(divData);
          }, 4000);
        }
      }, 50); // 滚动速度
    },
    warnFunc(param) {
      if (param.warnName === '报警信息') {
        return param.warnValue;
      } else {
        return param.warnName;
      }
    },
    oeeFunc(lpl, jdl, ct, cl, rt) {
      return (lpl * jdl * ((ct * cl) / (rt * 60)) / 100).toFixed(2);
    },
    getplanProData() {
      this.tableData = [];
      listPlannedProduction({
        keyWord: this.cutDevice.deviceCode,
        pageNum: 1,
        pageSize: 1
      }, {})
        .then(res => {
          if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
            this.cutDevice.planPro = res.rows[0]?.plannedProduction || 0;
            this.htmlDevState(this.devJSON);
          }
        });
    },
    // 获取OEE数据
    async getOeeData() {
      try {
        const params = {
          startTime: getSearchToday(0) + ' 00:00:00',
          endTime: getSearchToday(0) + ' 23:59:59',
          timeKeyEnum: 'DAY',
          queryRunTime: true,
          deviceCode: this.code
        };
        const res = await oeeChart(params, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          const { availability = 0, planCt = 0, actualQty = 0, runTime } = res.rows[0];
          var lpl;
          var jdl;
          if (res.rows[0].yield) {
            lpl = Number(res.rows[0].yield.toFixed(2));
          } else {
            lpl = 0;
          }

          if (availability) {
            jdl = Number(availability.toFixed(2));
          } else {
            jdl = 0;
          }
          let oeeVal = 0;
          if (lpl === 0 || jdl === 0) {
            oeeVal = 0;
          } else {
            oeeVal = this.oeeFunc(lpl, jdl, planCt, actualQty, runTime);
          }
          this.oeeChartData.full = 100;
          this.oeeChartData.value = oeeVal;
          this.oeeChartData.titleVal = oeeVal + '%';
        }
      } catch (error) {
        console.log(error);
      }
    },
    async showEchartsData() {
      if (this.devList.length === 0 || this.devLbIndex === this.devList.length - 1) {
        try {
          var condtion = {};
          // console.log('7788', localStorage.getItem('companyId'));
          if (this.getUrlKeyMain('cid', window.location.href)) {
            condtion.secretKey = this.kanbanConfig.key;
          }
          if (this.groupId > 0) {
            condtion.conditionJson1 = [
              {
                column: 'groupId',
                operator: 1,
                ptype: 'int',
                type: 'eq',
                value: this.groupId
              }
            ];
          }

          const res = await deviceRealLiveGetDeviceStateReportStatistics({
            current: 1,
            size: 99999,
            startTime: getTodayFirstDay(),
            endTime: getTodayLastDay()
          }, condtion
          );
          if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
            // console.log(333, res);
            this.devList = res.rows;
            this.getDeviceTimeRecentDayUtilization();
            this.devLbIndex = 0;
          }
        } catch (error) {
          console.log(error);
        }
      } else {
        this.getDeviceTimeRecentDayUtilization();
        this.devLbIndex++;
      }
    },
    async getDeviceTimeRecentDayUtilization() {
      try {
        var condtion = {
          conditionJson1: [
            {
              column: 'deviceCode',
              operator: 1,
              ptype: 'string',
              type: 'eq',
              value: this.devList[this.devLbIndex].deviceCode
            }
          ]
        };
        if (this.getUrlKeyMain('cid', window.location.href)) {
          condtion.secretKey = this.kanbanConfig.key;
        }
        // console.log('lalal', condtion);
        const res = await deviceReportGetDeviceDayUtilization({
          startTime: getSearchToday(7) + ' 00:00:00',
          endTime: getSearchToday(0) + ' 23:59:59'
        },
        condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // console.log('你好', res);
          var sbxl = [];
          var pjxl = [];
          var time = [];
          var all = [];
          var numAll = 0;
          for (var i = 0; i < res.rows.length; i++) {
            // pjxl.push(Number((data.dataset * 100).toFixed(2)));
            sbxl.push(Number((res.rows[i].value * 100).toFixed(2)));
            time.push(this.formatDateThree(res.rows[i].time));
            all.push(Number((res.rows[i].value * 100).toFixed(2)));
            numAll += res.rows[i].value;
          }
          var avg = ((numAll / all.length) * 100).toFixed(2);
          for (var x = 0; x < all.length; x++) {
            pjxl.push(Number(avg));
          }
          // console.log('大家好', pjxl);
          this.showEcharts(sbxl, pjxl, time, Math.min.apply(null, all) - 5, Math.max.apply(null, all) + 5, this.devList[this.devLbIndex].deviceName);
        }
      } catch (error) {
        console.log(error);
      }
    },
    async showDevManagerData() {
      var condtion = {};
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      if (this.groupId > 0) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'int',
            type: 'eq',
            value: this.groupId
          }
        ];
      }
      try {
        const res = await deviceManagerGetCheckCount({}, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          if (res.rows.waitCount === undefined) {
            this.checkCard.no = 0;
            this.checkCard.do = 0;
            this.checkCard.off = 0;
          } else {
            this.checkCard.no = res.rows.waitCount;
            this.checkCard.do = res.rows.ingCount;
            this.checkCard.off = res.rows.finishCount;
          }
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }

      try {
        const res = await deviceManagerGetRepairTaskCount({},
          condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.repairCard.no = res.rows.waitCount;
          this.repairCard.do = res.rows.ingCount;
          this.repairCard.off = res.rows.finishCount;
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }

      try {
        const res = await deviceManagerGetMaintainTaskCount({},
          condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.maintainCard.no = res.rows.waitCount;
          this.maintainCard.do = res.rows.ingCount;
          this.maintainCard.off = res.rows.finishCount;
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    async showEcharts2Data() {
      var condtion = {};
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      // if (this.groupId > 0) {
      condtion.conditionJson1 = [
        {
          column: 'groupId',
          operator: 1,
          ptype: 'list',
          type: 'in',
          value: this.groupId
        }
      ];
      // }
      try {
        const res = await deviceRealLiveGetDeviceRealState({}, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.showEcharts2(
            [
              { name: '运行', value: res.rows.runCount },
              { name: '空闲', value: res.rows.idleCount },
              { name: '离线', value: res.rows.offLineCount },
              { name: '告警', value: res.rows.warnCount }
            ]
          );
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    async showEcharts3Data() {
      var condtion = {
        conditionJson1: []
      };
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      condtion.conditionJson1 = [
        {
          column: 'groupId',
          operator: 1,
          ptype: 'list',
          type: 'in',
          value: this.groupId
        }
      ];
      try {
        const res = await deviceReportGetDeviceDayUtilization({
          startTime: getTodayFirstDay(),
          endTime: getTodayLastDay()
        }, condtion
        );
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var num = parseInt(res.rows[0].value * 100);
          this.showEcharts3(num);
          this.UtilizNum = num;
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    async showEcharts4Data() {
      // var group = 0;
      // if (this.allId) {
      //   group = this.allId;
      // } else {
      //   group = this.groupId;
      // }

      try {
        const res = await listWarnStatisticsChart({
          startTime: getSearchToday(0) + ' 00:00:00',
          endTime: getSearchToday(0) + ' 23:59:59',
          timeKeyEnum: 'LESSON',
          deviceCode: this.code
          // groupId: group
        }, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // console.log('OEE', res);
          var lpl = [];
          // var jdl = [];
          // var oee = [];
          var time = [];
          for (var i = 0; i < res.rows.length; i++) {
            lpl.push(Number(res.rows[i].radio.toFixed(0)));
            // jdl.push(Number(res.rows[i].availability.toFixed(2)));
            // oee.push(Number((res.rows[i].availability * res.rows[i].yield / 100).toFixed(2)));
            time.push(res.rows[i].key);
          }
          this.showEcharts4(lpl, time);
        }
      } catch (error) {
        console.log('OEE', error);
        console.log(error);
      }
    },
    async showEcharts6Data() {
      // var group = 0;
      // if (this.allId) {
      //   group = this.allId;
      // } else {
      //   group = this.groupId;
      // }
      try {
        const res = await oeeChart({
          startTime: getSearchToday(0) + ' 00:00:00',
          endTime: getSearchToday(0) + ' 23:59:59',
          timeKeyEnum: 'LESSON',
          deviceCode: this.code
          // groupId: group
        }, {});
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          // console.log('OEE', res);
          var jdl = [];
          var cl = [];
          var jhcl = [];
          var lpl = [];
          var jhlpl = [];
          // var jdl = [];
          // var oee = [];
          var time = [];
          for (var i = 0; i < res.rows.length; i++) {
            if (res.rows[i].availability) {
              jdl.push(Number(res.rows[i].availability.toFixed(2)));
            } else {
              jdl.push(0);
            }

            if (res.rows[i].yield) {
              cl.push(Number(res.rows[i].actualQty.toFixed(0)));
            } else {
              cl.push(0);
            }
            if (res.rows[i].planQty) {
              jhcl.push(Number(res.rows[i].planQty.toFixed(0)));
            } else {
              jhcl.push(0);
            }

            if (res.rows[i].yield) {
              lpl.push(Number(res.rows[i].yield.toFixed(2)));
            } else {
              lpl.push(0);
            }
            if (res.rows[i].planYield) {
              jhlpl.push(Number(res.rows[i].planYield.toFixed(2)));
            } else {
              jhlpl.push(0);
            }
            // jhlpl.push(Number(res.rows[i].planYield.toFixed(2)));

            // jdl.push(Number(res.rows[i].availability.toFixed(2)));
            // oee.push(Number((res.rows[i].availability * res.rows[i].yield / 100).toFixed(2)));
            time.push(res.rows[i].key);
          }
          this.showEcharts6(jdl, time);
          this.showEcharts7(cl, jhcl, time);
          this.showEcharts5(lpl, jhlpl, time);
        }
      } catch (error) {
        console.log('OEE', error);
        console.log(error);
      }
    },
    async showLineAndDev() {
      var condtion = {
        conditionJson1: [],
        current: 1,
        size: 99999999
      };
      if (this.getUrlKeyMain('cid', window.location.href)) {
        condtion.secretKey = this.kanbanConfig.key;
      }
      if (this.groupId) {
        condtion.conditionJson1 = [
          {
            column: 'groupId',
            operator: 1,
            ptype: 'list',
            type: 'in',
            value: this.groupId
          }
        ];
      }
      condtion.conditionJson1.push({
        column: 'isProductionDevice',
        operator: 1,
        ptype: 'int',
        type: 'eq',
        value: 1
      });
      try {
        const res = await deviceRealLiveGetPageByDevice({
          startTime: getTodayFirstDay(),
          endTime: getTodayLastDay()
        }, condtion);
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          var groupArr = [];
          var devJSON = [];
          // var temp = {};

          // console.log(res.rows);
          var data = [];
          var runData = [];
          for (var x = 0; x < res.rows.length; x++) {
            if (res.rows[x].realState !== 3) {
              runData.push(res.rows[x]);
            }
          }

          if (this.showRun) {
            data = runData;
          } else {
            data = res.rows;
          }

          // var data = res.rows;
          for (var i = 0; i < data.length; i++) {
            var lineName = '';
            if (data[i].productionLine) {
              lineName = data[i].productionLine;
            } else if (data[i].workshop) {
              lineName = data[i].workshop;
            } else if (data[i].factory) {
              lineName = data[i].factory;
            } else {
              lineName = '其他';
            }

            var index = groupArr.indexOf(lineName);
            if (index === -1) {
              groupArr.push(lineName);

              devJSON.push({
                lineName: lineName,
                devListData: [data[i]]
              });
            } else {
              devJSON[index].devListData.push(data[i]);
            }
          }
          this.devJSON = devJSON;
          if (devJSON[0].devListData.length > 0) {
            const info = devJSON[0].devListData.find(item => item.deviceName.includes('裁切检测包装机'));
            this.cutDevice.deviceCode = info.deviceCode;
            this.cutDevice.todayValue = this.getDeviceTodayValue(info);
            this.getplanProData(); // 获取裁切检测包装机计划产量
          }
        } else {
        }
      } catch (error) {
        // showErrorNotify(TITLE.NOTIFY, error);
      } finally { }
    },
    // 获取设备产量
    getDeviceTodayValue(deviceInfo) {
      let val = 0;
      if (deviceInfo.todayValue) {
        val = deviceInfo.todayValue;
      } else {
        var numcl = 0;
        var paramArr = deviceInfo.parameter;
        var hour = new Date().getHours();
        for (var z = 0; z < paramArr.length; z++) {
          if (hour < 8) {
            if (paramArr[z].parameterName === '生产计数白天总数据') {
              numcl += Number(paramArr[z].parameterValue);
            }
            if (paramArr[z].parameterName === '生产计数晚上总数据') {
              numcl += Number(paramArr[z].parameterValue);
            }
          } else if (hour < 20 && hour > 8) {
            if (paramArr[z].parameterName === '生产计数白天总数据') {
              numcl += Number(paramArr[z].parameterValue);
            }
          } else if (hour > 20) {
            if (paramArr[z].parameterName === '生产计数白天总数据') {
              numcl += Number(paramArr[z].parameterValue);
            }
            if (paramArr[z].parameterName === '生产计数晚上总数据') {
              numcl += Number(paramArr[z].parameterValue);
            }
          }
        }
        val = numcl;
      }
      return val;
    },
    htmlDevState(devJSON) {
      this.devStateHtml = '';

      var sbLine = 6;
      var cnum = 75;
      // var sbLine = 6;
      // var cnum = 24;

      var canWeb = true;
      if (window.innerWidth <= 1024) {
        sbLine = 6;
        cnum = 24;
        canWeb = false;
      } else if (window.innerWidth <= 1280) {
        sbLine = 8;
        cnum = 32;
        canWeb = false;
      }

      const height = (this.$refs.right1content.offsetHeight * 0.3) - cnum;

      var arr = devJSON;
      var html = '';

      for (var i = 0; i < arr.length; i++) {
        // 标记设备需要遍历几次
        var listNum = Math.ceil(arr[i].devListData.length / sbLine);
        // 标记第几次遍历
        var flag = 0;
        // 标记设备遍历长度
        var arrlength = 0;

        if (i !== 0) {
          var num = Math.ceil(arr[i - 1].devListData.length / sbLine);

          if (num >= 2) {
            if (canWeb) {
              html += '<div class="lineCON" style="margin-top:' + ((num * 1.1) * height) + 'px;" >';
            } else {
              html += '<div class="lineCON" style="margin-top:' + ((num - 1) * height) + 'px;" >';
            }
            // html += '<div class="lineCON" style="margin-top:' + ((num * 1.1) * height) + 'px;" >';
          } else {
            html += '<div class="lineCON" >';
          }
        } else {
          html += '<div class="lineCON" >';
        }

        html += '      <p class="lineName" >' + arr[i].lineName + '</p>';

        for (var j = 0; j < listNum; j++) {
          // 若设备长度与预设长度一致
          if (arr[i].devList <= sbLine) {
            arrlength += arr[i].devListData.length;
          } else {
            // 若下一次轮播设备数已经短于预设值
            if ((flag + 1) * sbLine > arr[i].devListData.length) {
              arrlength += (arr[i].devListData.length % sbLine);
            } else {
              arrlength += sbLine;
            }
          }
          flag++;
          html += '      <div class="lineDev" >';
          html += '        <div class="blackDev" >';

          for (var w = 0; w < sbLine; w++) {
            html += '       <div class="blackTask" ></div>';
          }
          html += '           <div class="blackLine" ></div>';
          html += '           </div>';
          html += '           <div class="blackState" >';
          for (var x = j * sbLine; x < arrlength; x++) {
            html += `<div onclick="toDevicePage('${arr[i].devListData[x].deviceCode}','${arr[i].devListData[x].deviceName}')" class="devTask state${arr[i].devListData[x].deviceRealState}">`;
            // html += '           <div class="devTask state' + arr[i].devListData[x].deviceRealState + '" >';
            if (arr[i].devListData[x].todayValue) {
              html += '          <span class="devTask_span2"  >' + arr[i].devListData[x].todayValue + '</span>';
            } else {
              var numcl = 0;
              var paramArr = arr[i].devListData[x].parameter;
              var hour = new Date().getHours();
              for (var z = 0; z < paramArr.length; z++) {
                if (hour < 8) {
                  if (paramArr[z].parameterName === '生产计数白天总数据') {
                    numcl += Number(paramArr[z].parameterValue);
                  }
                  if (paramArr[z].parameterName === '生产计数晚上总数据') {
                    numcl += Number(paramArr[z].parameterValue);
                  }
                } else if (hour < 20 && hour > 8) {
                  if (paramArr[z].parameterName === '生产计数白天总数据') {
                    numcl += Number(paramArr[z].parameterValue);
                  }
                } else if (hour > 20) {
                  if (paramArr[z].parameterName === '生产计数白天总数据') {
                    numcl += Number(paramArr[z].parameterValue);
                  }
                  if (paramArr[z].parameterName === '生产计数晚上总数据') {
                    numcl += Number(paramArr[z].parameterValue);
                  }
                }
              }
              html += '          <span class="devTask_span2"  >' + numcl + '</span>';
            }
            html += '          <p>' + arr[i].devListData[x].deviceName + '</p>';
            html += '           </div>';
          }
          html += `<div class="devTask planpro_box">
            <span class="devTask_span2">${this.cutDevice.planPro}</span>
            <p>计划产量</p>
          </div>`;
          html += `<div class="devTask ${this.getDiffState()}">
            <span class="devTask_span2">${this.cutDevice.todayValue - this.cutDevice.planPro}</span>
            <p>差值</p>
          </div>`;

          html += '          </div>';
          html += '      </div>';
        }
        html += '</div>';
      }
      this.devStateHtml = html;
      // $(".right1content").html(html);
      // setTimeout(() => {
      //   this.scrollDev();
      // }, 2500);
    },
    getDiffState() {
      if (this.cutDevice.todayValue - this.cutDevice.planPro > 0) {
        return 'devTask_green';
      } else if (this.cutDevice.todayValue - this.cutDevice.planPro < 0) {
        return 'devTask_red';
      } else {
        return 'devTask_yellow';
      }
    },
    toDevicePage(deviceCode, deviceName) {
      this.$router.push({ path: '/xwreport/capacity', query: { deviceCode, deviceName } });
